* {
  box-sizing: border-box; }

body {
  font-family: "Open Sans",sans-serif;
  font-size: 14px;
  height: 100%; }

a, a:focus, a:active, a:visited {
  color: #042E90;
  text-decoration: none;
  transition: color 0.3s ease-in-out; }

a:hover {
  color: #346ff9 !important; }

a.visited {
  color: #9E9E9E;
  transition: color 0.3s ease-in-out; }

a.active {
  color: #346ff9; }

h1, h2, h3, h4, h5 {
  font-family: "Raleway",sans-serif;
  padding: 0;
  margin: 0;
  font-weight: 700; }

h1 {
  font-size: 1.7rem;
  text-transform: uppercase; }

h2 {
  font-size: 1.5rem; }

#page-main {
  height: 100vh; }
  #page-main #page-wrapper {
    padding: 0 25px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: stretch; }
  #page-main #page-content {
    display: flex;
    height: 95%;
    overflow: hidden; }
    #page-main #page-content .right {
      width: 70%;
      min-width: 745px;
      padding: 20px 0 0 20px; }
      #page-main #page-content .right .gdpr-title {
        height: 10%;
        text-align: center; }
      #page-main #page-content .right .border {
        width: 100%;
        height: 90%;
        display: inline-block;
        border: 1px solid #e0e0e0; }
        #page-main #page-content .right .border iframe {
          width: 100%;
          height: 100%;
          border: none; }
    #page-main #page-content .left {
      display: flex;
      flex-direction: column;
      width: 30%;
      padding: 20px 20px 0 0; }
      #page-main #page-content .left #gdpr-lang {
        margin: 15px 0; }
        #page-main #page-content .left #gdpr-lang select {
          width: 100%;
          margin: 15px 0; }
      #page-main #page-content .left #gdpr-toc {
        display: flex;
        flex-direction: column;
        overflow: hidden; }
        #page-main #page-content .left #gdpr-toc .content {
          overflow-y: scroll;
          overflow-x: hidden; }
          #page-main #page-content .left #gdpr-toc .content ul {
            padding: 0 0 0 25px;
            margin: 0; }
            #page-main #page-content .left #gdpr-toc .content ul ul {
              padding: 10px 0 0 25px; }
          #page-main #page-content .left #gdpr-toc .content li {
            margin-bottom: 10px; }
          #page-main #page-content .left #gdpr-toc .content > ul {
            padding-right: 10px; }
            #page-main #page-content .left #gdpr-toc .content > ul > li > a {
              font-weight: 700;
              background-color: #ffe600; }
  #page-main #page-footer {
    height: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    border-top: 2px solid #f8ca01; }
    #page-main #page-footer .brainsum-loves-gdpr .icon {
      display: inline-block;
      width: 15px;
      height: 15px;
      vertical-align: middle; }
      #page-main #page-footer .brainsum-loves-gdpr .icon.heart {
        margin: 0 5px; }
